* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    color: white;
    background: white;
}

body, input, button, textarea {
    font: 400 18px 'Red Hat Text', sans-serif;
}

.dropd-item a {
    color: #518b46;
}

.bg-dark {
    background-color: #49883E !important;
    height: auto;
}

.nav-link {
    color: white !important;
}

.navbar-brand a {
    font-family: 'Montserrat', sans-serif;
    color: white !important;
    font-size: 40px;
}

.session-title {
    color: #36632e;
    margin-top: 30px;
}

#footer {
    height: 140px;
    width: 100%;
    background-color: #36632e;
    font-size: 12px;
    display: flex;
    flex: 1 1;
    align-items: center;
}

#footer .soc-networks {
    align-items: center;
}

#footer .soc-networks,
#footer .dev,
#footer .info {
    justify-content: space-between;
}

::-webkit-scrollbar {
    display: none;
}

@media (max-width: 320px) {
    .navbar-brand a {
        font-size: 30px;
    }
}
#page-landing {
    width: 100vw;
    height: 100vh;
    background:  linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)
      ), url(/static/media/bg.73dce167.jpg) no-repeat fixed;
    background-size: cover;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#page-landing nav {
    display: flex;
    width: 100%;

    height: 80px;
    background-color: rgba(0,0,0,0.3);

    margin: 0 auto;

    position: absolute;
    top: 0;

    justify-content: center;
    align-items: center;
}

#page-landing nav .nav-content {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1100px;
}

#page-landing nav .nav-content h3 {
    font-size: 45px;
    font-family: 'Montserrat', sans-serif;
}

#page-landing nav .nav-content .buttons {
    display: flex;
    width: 100%;
    max-width: 300px;
    height: auto;
    margin: auto 0;
    justify-content: space-between;
    
}

#page-landing nav .nav-content .buttons p {
    color: #f0f0f5;
    font-weight: 500;
    padding: 10px 20px;
    border: 1px solid #f0f0f5;
    border-radius: 16px;
    font-family: 'Red Hat Text', sans-serif;
    transition: 400ms;
}

#page-landing nav .nav-content .buttons p:hover {
    background: white;
    color: #49883E;
    border: 1px solid #49883E;
    transition: 400ms;
}

#page-landing #hero-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 100px;
}

#page-landing #hero-content h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 70px;
}

#page-landing #hero-content h3 {
    display: flex;
    color: white;
    width: 100%;
    max-width: 600px;
    font-size: 50px;
    padding: 10px 200px;
    border: 2px solid white;
    border-radius: 45px;
    transition: 600ms;
}

#page-landing #hero-content h3:hover {
    background: #f0f0f5;
    color: #49883E;
    border: 2px solid #49883E;
    transition: 600ms;
}

#page-landing .emailfield {
    display: none;
}
html {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
}

#main-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#main-page #cover {
    display: flex;
    width: 100%;
    height: 400px;
    background: linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)), url(/static/media/bg.73dce167.jpg) 90% no-repeat;
    background-size: cover;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 8px 12px rgba(73, 136, 62, 0.8);
}

#main-page #cover .cover-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1100px;
    line-height: 60px;
    text-align: center;
}

#main-page #cover .cover-content h1 {
    font-size: 4vw;
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}

#main-page #cover .cover-content h5 {
    font-family: 'Red Hat Display', sans-serif;
    font-weight: 400;
}

#main-page #cover .cover-content p {
    color: white;
    font-size: 22px;
    text-transform: uppercase;
    font-weight: 600;
    margin: 0;
}

#main-page #cover .cover-content a {
    display: flex;
    background-color: #49883E;
    width: 15%;
    margin: 0 auto;
    color: white;
    font-family: 'Red Hat Display', sans-serif;
    border: 2px solid rgba(73, 136, 62, 0.8);
    border-radius: 16px;
    justify-content: center;
    transition: 400ms;
}

#main-page #cover .cover-content a:hover {
    background-color: #37bf5c;
}

#main-page #categories {
    display: flex;
    margin: 80px 0;
    flex-direction: column;
    width: 100%;
    max-width: 70vw;
    align-items: center;
    justify-content: center;
    line-height: 80px;
}

#main-page #categories h1 {
    color: #49883E;
    font-family: 'Montserrat', sans-serif;
}

#main-page #categories #category {
    display: flex;
    width: 100%;
    height: 250px;
    background-size: cover;
    justify-content: center;
    align-items: center;
    border-radius: 32px;
    margin-bottom: 40px;
    box-shadow: 1px 6px 8px  rgba(73, 136, 62, 0.8);
}

#main-page #categories #category p {
    color: white;
    font-size: 30px;
    border: 1px solid white;
    text-align: center;
    padding: 2vh 3vw;
    border-radius: 16px;
    font-family: 'Red Hat Display', sans-serif;
    transition: 400ms;
}

#main-page #categories #category p:hover {
    background-color: rgba(0, 0, 0, 0.3);
}

.fitness-cat {
    background: linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)), url(/static/media/fitness.d407494b.jpg) 90% no-repeat;
}

.nutrition-cat {
    background: linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)), url(/static/media/nutricao.9e657e48.jpg) 90% no-repeat;
}

.herbs-cat {
    background: linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)), url(/static/media/ervas.dc7b9b1c.jpg) 90% no-repeat;
}

.beauty-cat {
    background: linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)), url(/static/media/beauty.d0fa8947.jfif) 90% no-repeat;
}

@media (min-width: 2480px) {
    #main-page #cover .cover-content h1{
        font-size: 60px;
    }
}

@media (max-width: 768px){
    #main-page #categories #category p {
        font-size: 16px;
        text-align: center;
        line-height: 30px;
    }
}

@media (max-width: 1024px){
    #main-page #cover .cover-content p {
        font-size: 18px;
    }
}

@media (max-width: 768px){
    #main-page #cover .cover-content p {
        font-size: 14px;
        margin: 0
    }

    #main-page #cover .cover-content a {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

@media (max-width: 426px){
    #main-page #cover .cover-content {
        line-height: 40px;

    }

    #main-page #cover .cover-content h5 {
        font-size: 14px;
    }

    #main-page #cover .cover-content h1 {
        font-size: 30px;
    }

    #main-page #cover .cover-content a {
        width: 30%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    #main-page #cover .cover-content p {
        font-family: 18px;
        margin: 0;
    }
}
